import './product-list-container.scss';
import BuchiImage from '../../../component/BuchiImage/BuchiImage';
import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
import commerce from '../../../lib/commerce';
import { Grid } from '@material-ui/core';
import { useNavigate } from 'react-router-dom';
import { useWindowDimension } from '../../../hooks/bubuchichis-hook';

const ProductCard = ({ product, onSignalIsLoaded }) => {
  const [isHover, setIsHover] = useState(false);
  const [loadCount, setLoadCount] = useState(0);
  const LOAD_COUNT = 2;
  const navigate = useNavigate();
  const redirectToProductPage = () => {
    const path = `/product/${product.name.replace(/\s+/g, '-')}-i.${product.permalink}`;
    navigate(path);
  };
  useEffect(() => {
    setLoadCount(num => num + 1);
  }, []);
  useEffect(() => {
    if (loadCount >= LOAD_COUNT) {
      onSignalIsLoaded();
    }
  }, [loadCount]);
  return (
    <div
      className={`product-card${isHover ? ' hover' : ''}`}
      onMouseEnter={() => setIsHover(true)}
      onMouseLeave={() => setIsHover(false)}
      onClick={() => redirectToProductPage()}
      onKeyDown={() => redirectToProductPage()}
      tabIndex="0"
      role="menuitem"
    >
      <div className="product-card__image-container">
        <BuchiImage className={`product-card__image${isHover ? ' hover' : ''}`} src={product.image?.url} alt={product.name} handleSignalIsLoaded={() => setLoadCount(num => num + 1)} />
      </div>
      <div className={`product-card__name${isHover ? ' hover' : ''}`}>{product.name}</div>
      <div className={`product-card__price${isHover ? ' hover' : ''}`}>{product.price?.formatted_with_code}</div>
    </div>
  );
};

ProductCard.propTypes = {
  product: PropTypes.object.isRequired,
  onSignalIsLoaded: PropTypes.func,
};

/** contains product grids */
const ProductGrids = ({ products, handleSignalIsLoaded }) => {
  const [loadedItemNum, setLoadedItemNum] = useState(0);
  let productsProcessed = [];
  const width = useWindowDimension()[0];
  const productsPerRow = width < 600 ? 2 : 3;
  const productCardSize = width < 600 ? 6 : 4;
  for (let i = 0; i < products.length; i += productsPerRow) {
    productsProcessed = productsProcessed.concat([products.slice(i, i + productsPerRow)]);
  }
  useEffect(() => {
    setLoadedItemNum(0);
  }, [loadedItemNum]);
  useEffect(() => {
    if (products.length && loadedItemNum >= products.length && handleSignalIsLoaded) {
      handleSignalIsLoaded();
    }
  }, [loadedItemNum]);

  return (
    <div className="product-list-container">
      <Grid container spacing={2}>
        {productsProcessed.map(row => (
          <Grid item container className="product-row" xs={12} spacing={2} key={`${row[0].id}_row`}>
            {row.map(item => (
              <Grid item xs={productCardSize} key={item.id}>
                <ProductCard product={item} onSignalIsLoaded={() => setLoadedItemNum(num => num + 1)} />
              </Grid>
            ))}
          </Grid>
        ))}
      </Grid>
    </div>
  );
};

ProductGrids.propTypes = {
  products: PropTypes.array.isRequired,
  handleSignalIsLoaded: PropTypes.func,
};

const ProductListContainer = ({ onSignalIsLoaded }) => {
  const [products, setProducts] = useState([]);
  useEffect(() => {
    fetchProducts();
  }, []);

  const fetchProducts = () => {
    commerce.products.list().then(response => {
      setProducts(response.data);
    }).catch((error) => {
      // eslint-disable-next-line
      console.log('There was an error fetching the products', error);
    });
  };

  return (
    <Grid container className="page-width">
      <Grid item xs={12}>
        <h1 className="page-title">Our Products</h1>
      </Grid>
      <Grid item xs={12}>
        <ProductGrids
          products={products}
          handleSignalIsLoaded={onSignalIsLoaded}
        />
      </Grid>
    </Grid>
  );
};

ProductListContainer.propTypes = {
  onSignalIsLoaded: PropTypes.func,
};

export default ProductListContainer;
